








import Vue from 'vue'

export default Vue.extend({
  name: 'TagIcon',
  props: {
    title: {type: String},
  },
  data: () => ({
    unicode: ''
  }),

  beforeMount () {
    if (this.title === 'resources') this.unicode = '📑'
    else if (this.title === 'tips') this.unicode = '📋'
    else if (this.title === 'recommendations') this.unicode = '📋'
    else if (this.title === 'politics') this.unicode = '🗳️'
    else if (this.title === 'art') this.unicode = '🎨'
    else if (this.title === 'data') this.unicode = '🗄️'
    else if (this.title === 'remotework') this.unicode = '💻'
    else if (this.title === 'food') this.unicode = '🍲'
    else if (this.title === 'films') this.unicode = '📽️'
    else if (this.title === 'movies') this.unicode = '📽️'
    else if (this.title === 'coronavirus') this.unicode = '😷'
    else if (this.title === 'games') this.unicode = '🎮️'
    else if (this.title === 'freelance') this.unicode = '🖥️️'
    else if (this.title === 'teaching') this.unicode = '👩‍🏫'
    else if (this.title === 'healthcare') this.unicode = '⚕️'
    else if (this.title === 'jobs') this.unicode = '👔️'
    else if (this.title === 'parenting') this.unicode = '👪️'
    else if (this.title === 'music') this.unicode = '🎵'
    else if (this.title === 'sport') this.unicode = '⚽️'
    else if (this.title === 'journalism') this.unicode = '📰️'
    else if (this.title === 'guide') this.unicode = 'ℹ️️'
    else if (this.title === 'animal crossing') this.unicode = '🌻️'
    else if (this.title === 'ACNH') this.unicode = '🌻'
    else if (this.title === 'finance') this.unicode = '💸️'
    else if (this.title === 'startup') this.unicode = '🦄️'
    else if (this.title === 'fitness') this.unicode = '💪️'
    else if (this.title === 'films') this.unicode = '📽️'
    else if (this.title === 'VC') this.unicode = '🦄️️'
    else if (this.title === 'climate') this.unicode = '🌍️'
    else if (this.title === 'recipe') this.unicode = '🍲'
    else if (this.title === 'recruitment') this.unicode = '👔️'
    else if (this.title === 'vegan') this.unicode = '🌱️'
    else if (this.title === 'aviation') this.unicode = '✈️'
    else if (this.title === 'theatre') this.unicode = '🎭'
    else if (this.title === 'history') this.unicode = '🏺'
    else if (this.title === 'reading') this.unicode = '📚'
    else if (this.title === 'technology') this.unicode = '⚙️'
    else if (this.title === 'socialism') this.unicode = '🌹️'
    else if (this.title === 'fact-check') this.unicode = '🔍'
    else if (this.title === 'protest') this.unicode = '✊️'
    else if (this.title === 'travel') this.unicode = '🧳️'
    else if (this.title === 'books') this.unicode = '📚️'
    else if (this.title === 'mental health') this.unicode = '💚️'
    else if (this.title === 'podcasts') this.unicode = '🎙️️'
    else if (this.title === 'contacts') this.unicode = '📝'
    else if (this.title === 'media') this.unicode = '🔊'
    else if (this.title === 'society') this.unicode = '🏛️'
    else if (this.title === 'research') this.unicode = '🔬'
    else if (this.title === 'LGBT') this.unicode = '🏳️‍🌈'
    else if (this.title === 'programming') this.unicode = '⌨️️️'
    else if (this.title === 'learning') this.unicode = '👩‍🏫'
    else if (this.title === 'AI') this.unicode = '🧠️️'
    else if (this.title === 'gender') this.unicode = '♀️️️'
    else if (this.title === 'UK') this.unicode = '<img style="height: 100%" src="data:image/png;base64,"/>'
    else if (this.title === 'Hong Kong') this.unicode = '<img style="height: 100%" src="data:image/png;base64,"/>'
    else if (this.title === 'USA') this.unicode = '<img style="height: 100%" src="data:image/png;base64,"/>'
    else if (this.title === 'India') this.unicode = '<img style="height: 100%" src="data:image/png;base64,"/>'
  }, //icons8.com

  mounted () {

  },

  methods: {

    textToColor (text) {
      let hash = 0
      for (let i = 0; i < text.length; i++) {
        hash = text.charCodeAt(i) + ((hash << 5) - hash)
      }
      const color = Math.floor(Math.abs((Math.sin(hash) * 10000) % 1 * 16777216)).toString(16)
      return '#' + Array(6 - color.length + 1).join('0') + color
      // return `hsl(${hashCode(text) % 360}, 10%, 50%)`;
    }

  }

})

